import { Navigate, Route, Routes } from "react-router-dom";
import "./App.css";
import Mealplanner from "./components/Mealplanner";
import Login from "./components/login/Login";
import MealWeekPlan from "./components/MealWeekPlan";
import MealSearch from "./components/MealSearch";
import MealCards from "./components/MealCards";
import Product from "./components/Product";
import PDFfile from "./components/PDFfile";

function App() {
  console.log(process.env, "process");
  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Navigate to="/Meal_Planner" />} />
        <Route path="/Meal_Planner" element={<Login />} />
        <Route path="/Meal_Planner/mealplanner" element={<Mealplanner />}>
          <Route path="mealsearch" element={<MealSearch />} />
          <Route path="mealview" element={<MealCards />} />
        </Route>
        <Route path="/Meal_Planner/product/:name" element={<Product />} />
        <Route path="/Meal_Planner/pdf" element={<PDFfile />} />
        <Route path="/Meal_Planner/weekplan" element={<MealWeekPlan />} />
      </Routes>
    </div>
  );
}

export default App;
